.faq-page {
    width: 100%;
    height: auto;
    background: black;
    color: #fff;
    font-family: Arial, sans-serif;
    position: relative;
    margin-top: 15%;
}

.faq-title {
    padding-top: 3%;
    font-size: 100px;
    font-weight: bold;
    color: #009a0d;
    text-align: center;
    background: transparent;
    background-color: transparent;
    color: black;
    -webkit-text-stroke: 1px rgba(53, 216, 36, 1);
}

.faq-items {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding-left: 18%;
    background: transparent;
    position: relative;

}

.faq-item:last-child {
    align-self: flex-start;
    margin-right: 40%;
}


.faq-item {
    width: 40%;
    text-align: left;
    background: transparent;

}



.faq-item-title {
    font-size: 34px;
    font-weight: 500;
    margin-bottom: 10px;
    background: transparent;

}

.faq-item-text {
    width: 70%;
    font-size: 30.5px;
    line-height: 45px;
    font-family: "Macondo";
    font-weight: 400;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
    background: transparent;


}

.social-section {
    padding-left: 9%;
    text-align: center;
    margin-top: 0px;
    background: transparent;

}

.social-icons {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 10px;
    background: transparent;

}

.social-icons img {
    width: 120px;
    height: 120px;
    background: transparent;
    transition: transform 0.3s ease;
}


.social-text {
    background: transparent;
    font-size: 46px;
    color: #aaa;
    font-family: "Macondo";
}

