@media (min-width: 481px) and (max-width: 768px),
(min-height: 601px) and (max-height: 800px)  {

    .social-text {
        font-size: 13px;
    }

    .social-icons img {
        width: 30px;
        height: 30px;
    }

    .faq-item-text {
        font-size: 13px;
        line-height: 20px;

    }

    .faq-item-title {
        font-size: 15px;
    }

    .faq-title {
        font-size: 30px;
        -webkit-text-stroke: 0.1px rgba(53, 216, 36, 1);

    }

    .faq-item {
        width: 50%;
    }
} 

@media (max-width: 480px), (max-height: 600px)  {
    .social-section {
        position: relative;
        margin-top: -20%;
        margin-left: 35%;
    }
    .social-text {
        font-size: 12px;
    }

    .social-icons img {
        width: 30px;
        height: 30px;
    }

    .faq-item-text {
        font-size: 11px;
        line-height: 15px;
        text-align: center;
        margin-left: 10%;

    }

    .faq-item-title {
        font-size: 13px;
        text-align: center;
    }

    .faq-title {
        font-size: 25px;
        -webkit-text-stroke: 0.1px rgba(53, 216, 36, 1);

    }

    .faq-item {
        width: 50%;
    }
    .faq-items{
        padding: 5%;
        background-color: black;
    }
    .faq-items :last-child {
        width: 70%;
        text-align: start;
    }
}